import React, {propTypes, Component} from 'react';

export default class Map extends Component{
    constructor(props){
        super(props);

        let clientWidth = document.documentElement.clientWidth;
        let clientHeight = document.documentElement.clientHeight;

        this.state = {
            clientWidth,
            clientHeight,
            grid: [[]],
            countColumn: Math.floor(clientHeight / 50),
            countRows: Math.floor(clientWidth / 50),

        };
    }


    render(){



        // for(let i (0);i<)
        return(
            <section className="map">
                {`${this.state.countRows} : ${this.state.countColumn}`}
            </section>
        );
    }
}